<template lang="html">
  <x-header>
    {{team.name}}
    <a v-if="isOwner" slot="right" v-link="{name: 'teamsedit'}">
      编辑
    </a>
  </x-header>
  <div class="team_logo">
    <div class="logo">
      <img :src="team.logo" alt="" />
    </div>
  </div>
  <div class="weui_cells">
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-at"></i>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{team.name}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-tag"></i>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{team.sign}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
  </div>
  <div class="weui_cells_title">{{team.name}}的球员：</div>
    <div class="weui_cells">
      <div class="weui_cell" v-for="user in team.users">
        <div class="weui_cell_hd">
          <img :src="user.avatar" style="width:20px;margin-right:5px;display:block">
        </div>
        <div class="weui_cell_bd weui_cell_primary">
          <p>{{user.name}}</p>
        </div>
        <div class="weui_cell_ft">身高{{user.height}}cm</div>
      </div>
    </div>
  </div>
  <div class="button_sp_area" style="margin-top:20px;padding: 10px;">
    <a @click="joinTeam" href="javascript:;" class="weui_btn weui_btn_plain_primary">加入队伍</a>
  </div>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import * as teamService from '../../services/teamService'
  import * as teamUserService from '../../services/teamUserService'
  import { showToast } from '../../vuex/actions'

  export default {
    vuex: {
      actions: {
        showToast
      }
    },
    data: function () {
      return {
        team: {
          users: [],
          owner: {}
        }
      }
    },
    route: {
      data (transition) {
        teamService.getTeam(this.$route.params.tid)
        .then((responseJson) => {
          this.$set('team', responseJson.team)
        })
        .catch((error) => {
          this.showToast(error)
        })
      }
    },
    methods: {
      joinTeam () {
        let token = localStorage.getItem('ruzun_user_token')
        if (token === null) {
          this.$route.router.go({name: 'signin'})
          return
        }
        teamUserService.createTeamUser({team_id: this.$route.params.tid}, token)
        .then((responseJson) => {
          this.team.users.push(responseJson.user)
          this.showToast('加入成功！')
        })
        .catch((error) => {
          this.showToast(error)
        })
      }
    },
    computed: {
      isOwner () {
        let userId = localStorage.getItem('ruzun_user_id') || 0
        return this.team.owner.id === parseInt(userId)
      }
    },
    components: {
      XHeader
    }
  }
</script>

<style lang="scss">
</style>



